.tab {
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: rgba(41, 41, 41, 0.6);
    padding: 10px 0;
    margin-right: 20px;
    cursor: pointer;
}

.tab:last-child {
    margin-right: 0;
}

.tab_type_current {
    color: #3C39EC;
    box-shadow: inset 0px -2px 0px #3C39EC;
}

.onHover {
    box-shadow: inset 0px -2px 0px #3C39EC;
}

.tabs {
    display: flex;
    width: 100%;
    box-shadow: inset 0px -1px 0px rgba(41, 41, 41, 0.13);
}