.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.title {
    font-style: normal;
    font-weight: 600;
    font-size: 32px;
    line-height: 32px;
    margin-right: 20px;
}

@media (max-width: 400px) {
    .title {
        font-size: 24px;
    }
}